  vertical-align: middle; }

.row-line.top > div {
  vertical-align: top; }

.row-line.bottom > div {
  vertical-align: bottom; }

.row-line.baseline > div {
  vertical-align: baseline; }

.row-line.row-reverse {
  direction: rtl; }

.row-line.row-reverse > div {
  direction: ltr; }

/* 1.1.3 flex */
