
.contacts {
    @media (max-width: #{map-get($devices, 'ds_')-1}px) {
        padding-top: 1*$l_h_base;
    }
    .address {
        @include v_rhytm('item', $l_h);
        @include height_leveling('text-compressed');
        @include font_size('fb');
        font-weight: 500;
    }
    .phones {
        @include v_rhytm('item', $l_h);
        &__item {
            @include height_leveling('text-compressed');
            @include font_size('fb');
            font-weight: 500;
            a {
                @include color_scheme('dark');
                &:hover {
                    @include color_scheme('accent');
                }
                @media (min-width: #{map-get($devices, 'ds_')}px) {
                    @include color_scheme('default');
                    &:hover {
                        @include color_scheme('accent');
                    }
                }
            }
        }
    }
}

.contacts--footer {
    padding: 0;
    margin-top: 0.5*$l_h_base;
    .address {
        @include font_size('small');
        @include color_scheme('dark');
        font-weight: 400;
    }
    .phones {
        &__item {
            @include font_size('small');
            font-weight: 400;
            a {
                @include color_scheme('dark');
                &:hover {
                    @include color_scheme('accent');
                }
            }
        }
    }
    .contacts__title {
        @extend %footer__title;
    }
}
