@charset "UTF-8";
/*'../fonts/ukresult/'*/

@font-face {
  font-family: "ukresult";
  src:url("../fonts/ukresult/ukresult.eot");
  src:url("../fonts/ukresult/ukresult.eot?#iefix") format("embedded-opentype"),
    url("../fonts/ukresult/ukresult.woff") format("woff"),
    url("../fonts/ukresult/ukresult.ttf") format("truetype"),
    url("../fonts/ukresult/ukresult.svg#ukresult") format("svg");
  font-weight: normal;
  font-style: normal;

}

